	module.exports = {"data":{"markdownRemark":{"html":"<p>In processing I can grab an image of the sky from one of the webcams around Austin and load the pixels in to an array.</p>\n<p><img src=\"https://s3.amazonaws.com/rewferguson.com/img/Weather-Lamp/KXAN02.jpg\" alt=\"webcam image\"></p>\n<p>Its then easy to average out the sky pixels to get one rgb value.</p>\n<p><img src=\"https://s3.amazonaws.com/rewferguson.com/img/Weather-Lamp/output.png\" alt=\"weather lamp processing output\"></p>\n<p>This doesn’t really make a pleasing lamp though.  The color temperature is just way too blue.  I’ll have to find another way of turning an image of the sky into a pleasing lamp color.</p>","frontmatter":{"title":"Averaging the sky color from a photo"}},"projectInfo":{"edges":[{"node":{"name":"Weather Lamp","folder":"weather-lamp","status":2,"image":"https://s3.amazonaws.com/rewferguson.com/img/Weather-Lamp/header.svg","description":"I picked an office with no windows, so I’m stuck with artificial light only while I work. I’d like some ambient light provided with RGB LEDs that change their color temperature based on the local weather outside. I’m sure there are lamps that do this, but I would like the light more integrated into my work area. Not one single spot of colored light, but an overall ambient glow. I figure to get this effect I would have to build something myself.","tags":["processing"],"github":null,"link":null}}]},"currentProject":{"edges":[{"node":{"frontmatter":{"title":"sketch","date":null},"fields":{"slug":"/projects/weather-lamp/2015-03-07-sketch/"},"excerpt":"","html":"<p><img src=\"https://s3.amazonaws.com/rewferguson.com/img/Weather-Lamp/sketch.jpg\" alt=\"weatherLamp sketch\"></p>"}},{"node":{"frontmatter":{"title":"Working with Arduino Pro Mini","date":null},"fields":{"slug":"/projects/weather-lamp/2015-05-08-working-with-arduino-pro-mini/"},"excerpt":"","html":"<p><img src=\"https://s3.amazonaws.com/rewferguson.com/img/Weather-Lamp/IMG_1175.jpg\" alt=\"Arduino Pro Mini\"></p>"}},{"node":{"frontmatter":{"title":"Averaging the sky color from a photo","date":null},"fields":{"slug":"/projects/weather-lamp/2015-05-04-averaging-the-sky-color-from-a-photo/"},"excerpt":"In processing I can grab an image of the sky from one of the webcams around Austin and load the pixels in to an array. Its then easy to…","html":"<p>In processing I can grab an image of the sky from one of the webcams around Austin and load the pixels in to an array.</p>\n<p><img src=\"https://s3.amazonaws.com/rewferguson.com/img/Weather-Lamp/KXAN02.jpg\" alt=\"webcam image\"></p>\n<p>Its then easy to average out the sky pixels to get one rgb value.</p>\n<p><img src=\"https://s3.amazonaws.com/rewferguson.com/img/Weather-Lamp/output.png\" alt=\"weather lamp processing output\"></p>\n<p>This doesn’t really make a pleasing lamp though.  The color temperature is just way too blue.  I’ll have to find another way of turning an image of the sky into a pleasing lamp color.</p>"}}]}},"pathContext":{"slug":"/projects/weather-lamp/2015-05-04-averaging-the-sky-color-from-a-photo/","folder":"/weather-lamp/","folderName":"weather-lamp"}}

